// 大小
$footer-height: 300px;
// 响应式变换点
$pad-size: 1024px;
$phone-size: 414px;
.interlude-bg {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 100%;
  right: 0;
  background: $stroke-green;
  transition: 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
  z-index: 1;
}
.interlude-bg-active {
  @media screen and(max-width: $pad-size) {
    left: 0;
  }
  left: 240px;
}

.interlude-img-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 100%;
  right: 0;
  overflow: hidden;
  z-index: 1;
  transition: 0.5s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.interlude-img-container-active {
  @media screen and(max-width: $pad-size) {
    left: 0;
  }
  left: 240px;
}

.interlude-mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}

.interlude-img {
  position: absolute;
  right: 0;
  height: 100%;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
}