.menu-item {
  position: relative;
  display: block;
  height: 60px;
  margin-bottom: 15px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%; }
  .menu-item .mask {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(214, 28, 143, 0.5);
    -webkit-transform: translate3d(-10px, 6px, 0);
            transform: translate3d(-10px, 6px, 0);
    -webkit-transition: 0.3s;
    transition: 0.3s; }
  .menu-item:hover {
    -webkit-transform: scale(1.07, 1.1);
            transform: scale(1.07, 1.1); }
    .menu-item:hover .menu-item-link {
      color: #fff; }
    .menu-item:hover .mask,
    .menu-item:hover .menu-item-bg {
