  font-weight: 400;
  text-decoration: none;
  color: #222;
  padding: 0 30px;
  z-index: 1;
  -webkit-transition: 0.3s;
  transition: 0.3s; }

.menu-item-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  opacity: 0;
  -webkit-transform: translate3d(-10px, 6px, 0);
          transform: translate3d(-10px, 6px, 0);
  -webkit-transition: 0.3s;
  transition: 0.3s; }

